<template>
  <div class="homepage" id="homepage-scroll-check">
    <div class="row">
      <div class="width-center">Invest in</div>
      <div class="type-list">
        <transition-group name="list-complete">
          <div v-for="type in creatorTypeList" :key="type" :ref="'creator-'+type" class="list-complete-item">
            {{type}}
          </div>
        </transition-group>
      </div>
    </div>
    <div class="picture-pane" id="creator-picture-pane">
      <transition appear name="fade-appear-1">
        <img class="creator-img" id="sliding-creator-img0" src="@/assets/youtuber-3.jpg">
      </transition>
      <transition appear name="fade-appear-2">
        <img class="creator-img hide-img" src="@/assets/youtuber-1.jpg">
      </transition>
      <transition appear name="fade-appear-2">
        <img class="creator-img hide-img hide-copy" id="sliding-creator-img1" src="@/assets/youtuber-1-crop.jpg">
      </transition>
      <transition appear name="fade-appear-3">
        <img class="creator-img hide-img" id="sliding-creator-img2" src="@/assets/youtuber-2.jpg">
      </transition>
    </div>
    <div class="text-pane">
      <div class="info-box hidden-low" v-infocus="'showElement'">
          <div class="box-header">
            Investing, made personal
          </div>
          Traditional stocks can feel like throwing a cup of water into the ocean. Bazzled connects you to your favorite content creators, where every penny helps them make the best content possible.
          <div class="box-header-low">
            Have a channel?
          </div>
          <router-link to="/partner" class="get-started">
            Partner with us
          </router-link>
        </div>
    </div>
    <div class="safe-pane">
      <div class="row-safe" >
        <div class="safe-block hidden-low" v-infocus="'showElement'">
          <i class="fab fa-ethereum safe-icons"></i>
          <div class="box-header"> Safe </div>
          <div class="safe-paragraph"> Every investment is backed by an unbreakable ethereum smart&#8209;contract</div>
        </div>
        <div class="safe-block hidden-low" v-infocus="'showElement2'">
          <i class="fas fa-lock safe-icons"></i>
          <div class="box-header"> Secure </div>
          <div class="safe-paragraph"> We use advanced security systems to protect ourselves and our users</div>
        </div>
        <div class="safe-block hidden-low" v-infocus="'showElement3'">
          <i class="fas fa-user-shield safe-icons"></i>
          <div class="box-header"> Private </div>
          <div class="safe-paragraph"> Bazzled values privacy, and never sells any user data </div>
        </div>
      </div>
    </div>
    <div class="graph-pane" id="graph-c">
      <div class="left-col hidden-low" v-infocus="'showElement'">
        <div class="box-header">
          Why wait?
        </div>
        <div class="safe-paragraph">
          Don't let your money rot in a bank. Help creators early on and share in their success.
        </div>
        <div class="start-now">
          <router-link to="/signup" class="get-started">
            Start now!
          </router-link>
        </div>
      </div>
      <div class="right-col hidden-low" v-infocus="'showElement'">
        <div style="height: 250px;">
          <transition name="fade" mode="out-in">
            <img :src="require('@/assets/logo-'+curYoutuber)" class="youtuber-logo" :key="curYoutuber" :alt="curYoutuber" :title="curYoutuber.split('.')[0]">
          </transition>
        </div>
        <canvas id="graph" width="550" height="330"></canvas>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data(){
    return{
      creatorTypeList: ['-', 'gamers.', 'vloggers.', 'reviewers.',  'DIYers.', 'artists.', 'comedians.', 'cooks.', 'teachers.', 'people.'],
      startGraph: false,
      curYoutuber: "mrbeast.png",
      curYoutuberList: ["mrbeast.png", "dream.jpg", "pewdiepie.png"],
      stopGraphDraw: false,
      curImg: 0,
      killAllAsync: false,
    }
  },
  mounted(){
    document.getElementById('navbar-border').style.borderBottom = '1px solid white';
    this.$refs['creator-'+this.creatorTypeList[1]].style.opacity = 1;
    this.$refs['creator-'+this.creatorTypeList[2]].style.opacity = .5;
    this.$refs['creator-'+this.creatorTypeList[3]].style.opacity = .2;
    this.$refs['creator-'+this.creatorTypeList[4]].style.opacity = .05;
    this.$refs['creator-'+this.creatorTypeList[this.creatorTypeList.length-1]].style.color = '#4285F4';
    setTimeout(() => this.removeTop(), 1000);
    setTimeout(() => this.slidePictures(), 6000);
  },
  methods: {
    removeTop: function(){
      //deals with error thrown if user switches pages before animation is complete
      if(!this.$refs['creator-'+this.creatorTypeList[0]])
        return;
      this.$refs['creator-'+this.creatorTypeList[0]].style.opacity = 0;
      this.creatorTypeList.splice(0,1);
      this.$refs['creator-'+this.creatorTypeList[0]].style.opacity = 0.2;
      this.$refs['creator-'+this.creatorTypeList[1]].style.opacity = 1;
      if(this.creatorTypeList.length > 2){
        this.$refs['creator-'+this.creatorTypeList[2]].style.opacity = .5;
        if(this.creatorTypeList.length > 3)
          this.$refs['creator-'+this.creatorTypeList[3]].style.opacity = .2;
        if(this.creatorTypeList.length > 4)
          this.$refs['creator-'+this.creatorTypeList[4]].style.opacity = .05;
        setTimeout(() => this.removeTop(), 1000);
      }
      else{
        setTimeout(() => {this.$refs['creator-'+this.creatorTypeList[0]].style.opacity = 0;}, 300);
      }
    },
    addNavBorder: function(){
      if(window.scrollY == 0)
        document.getElementById('navbar-border').style.borderBottom = '1px solid white';
      else
        document.getElementById('navbar-border').style.borderBottom = '1px solid #e5e5e5';
      if(window.scrollY >= 1470 && !this.startGraph){
        this.startGraph = true;
        this.drawGraph();
      }
    },
    slidePictures: async function(){
      const width  = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth;
      if(this.killAllAsync) 
        return;
      if(width >= 1130){
        document.getElementById('sliding-creator-img' + 0).style.width = '';
        document.getElementById('sliding-creator-img' + 0).style.opacity = '1';
        document.getElementById('sliding-creator-img' + 1).style.width = '';
        document.getElementById('sliding-creator-img' + 1).style.opacity = '1';
        document.getElementById('sliding-creator-img' + 2).style.width = '';
        document.getElementById('sliding-creator-img' + 2).style.opacity = '1';
        return;
      }
      setTimeout(() => {
        if(this.killAllAsync)
          return;
        document.getElementById('sliding-creator-img' + this.curImg).style.opacity = '0';
        this.curImg = (this.curImg + 1) % 3
        setTimeout(() => document.getElementById('sliding-creator-img' + this.curImg).style.opacity = '1', 300);
        this.slidePictures();
      }, 4000);
    },
    drawGraph: async function(){
      //parameters:
      //0: mrBeast, 1: Dream, 2: PewDiePie
      const yPointsList = [[299, 296, 287, 224, 137, 10],[299, 299, 299, 288, 121, 10],[299, 296, 276, 172, 66, 10]]
      const yPointsTextList = [['16k', '','2M', '13M', '28M', '50M'], ['1k', '', '', '1M', '16M', '26M'], ['1k', '','3.3M', '18M', '33M', '41M']];
      const xPointsTextList = [[2016, 2017, 2018, 2019, 2020, 2021], [2017, 2018, 2019, 2020, 2021, "Sept '21"], [2011, 2012, 2013, 2014, 2015, 2016]];
      const  xPoints = [0, 100, 200, 300, 400, 500]
      const redDelay = 200;
      const speed = .015;
      const axisSpeed = .03;

      await wait(1000);
      if(this.killAllAsync) return;
      var graph = document.getElementById('graph');
      var c = graph.getContext('2d')
      graph = c.canvas;
      c.font = 'Open Sans 10pt sans-serif';
      c.textAlign = "center";
      for(let n = 0; n < 3; n++){
        this.curYoutuber = this.curYoutuberList[n];
        //set yPoints and x/y text
        var yPoints = yPointsList[n];
        var yPointsText = yPointsTextList[n];
        var xPointsText = xPointsTextList[n];
        //draw x, y axis
        c.lineWidth = 2;
        c.strokeStyle = '#333';
        animate(0,300,0,0,0,axisSpeed,this);
        await animate(0,300,500,300,0,axisSpeed,this);
        if(this.killAllAsync) return;
        //red
        c.strokeStyle = '#ff0000';
        let x1 = xPoints[0], y1 = yPoints[0];
        c.lineWidth = 3;
        c.fillText(yPointsText[0],10,y1);
        c.fillText(xPointsText[0],x1+30,graph.height-10);
        for(let i = 1; i < xPoints.length; i++){
          if(this.stopGraphDraw)
            return;
          await wait(redDelay);
          if(this.killAllAsync) return;
          await animate(x1,y1, xPoints[i], yPoints[i], 0, speed, this);
          if(this.killAllAsync) return;
          x1 = xPoints[i];
          y1 = yPoints[i];
          c.fillText(yPointsText[i],10,y1);
          c.fillText(xPointsText[i],x1+30,graph.height-10);
        }
        await wait(1500);
        if(this.killAllAsync) return;
        c.clearRect(0, 0, graph.width, graph.height);  
        if(n == 2)
          n = -1;
      }

      function drawLine(x1,y1,x2,y2,ratio) {
        x1 += 30; 
        x2 += 30;
        c.beginPath();
        c.moveTo(x1,y1);
        x2 = x1 + ratio * (x2-x1);
        y2 = y1 + ratio * (y2-y1);
        c.lineTo(x2,y2);
        c.stroke();
      }

      function animate(x1, y1, x2, y2, ratio, speed, instance) { return new Promise((resolve) => {
        speed = speed || .01;
        ratio = ratio || 0;
        drawLine(x1,y1,x2,y2,ratio);
        if(ratio<1) {
          requestAnimationFrame(() => {
            if(instance.killAllAsync) return;
            animate(x1,y1,x2,y2,ratio + speed,speed,instance).then(() => {return resolve('done')});
          });
        }
        else return resolve('done');
      });
      }

      function wait(t) { return new Promise((resolve) => {
        setTimeout(() => resolve('waited'), t)
      });
      }
    },
  },
  created: function() {
    window.addEventListener('scroll',this.addNavBorder);
    this.stopGraphDraw = false;
  },
  beforeUnmount: function(){
    this.killAllAsync = true;
  },
  unmounted: function() {
    document.getElementById('navbar-border').style.borderBottom = '1px solid #e5e5e5';
    window.removeEventListener('scroll', this.addNavBorder);
    this.stopGraphDraw = true;
  },
  directives: {
    infocus: {
      isLiteral: true,
      mounted: (el, binding) => {
        let f = () => {
          let rect = el.getBoundingClientRect()
          let inView = (
            rect.width > 0 &&
            rect.height > 0 &&
            rect.top >= 0 &&
            (rect.top + 100) <= (window.innerHeight || document.documentElement.clientHeight)
          )
          if (inView) {
            el.classList.add(binding.value)
            window.removeEventListener('scroll', f)
          }
        }
        window.addEventListener('scroll', f)
        f()
      }
    }
  }
}
</script>

<style scoped>
  .homepage{
    padding-top: 180px;
  }
  .row{
    display: flex;
    justify-content: center;
    font-size: 64px;
    font-weight: 600;
    margin-bottom: 100px;
  }
  .width-center{
    width: 360px;
    text-align: right;
  }
  .type-list{
    overflow: hidden;
    height: 375px;
    text-align: left;
    margin-left: 20px;
    width: 360px;
    margin-top: -74px;
    z-index: 9;
  }
  .list-complete-item {
    opacity: 0;
    transition: all 0.7s;
  }
  .list-complete-enter-from,
  .list-complete-leave-to {
    transform: translateY(-74px);
  }
  .list-complete-leave-active {
    position: absolute;
  }
  /* new-pane */
  .creator-img{
    min-width: 355px;
    max-width: 460px;
    width: calc(33% - 40px);
    margin-right: 10px;
    border-radius: 4px;
    transition: 1s all;
  }
  /* new-pane */
  .text-pane{
    padding: 160px 0;
    text-align: center;
    max-width: 650px;
    margin: auto;
  }
  .info-box{
    font-size: 24px;
    text-align: center;
    color: var(--dark-gray);
    line-height: 140%;
    padding: 0 25px;
  }
  .box-header{
    font-size: 40px;
    font-weight: 700;
    margin-bottom: 16px;
  }
  .box-header-low{
    font-size: 40px;
    font-weight: 700;
    margin: 80px 0 40px;
  }
  .get-started{
    color: white;
    background: var(--primary-color);
    border-radius: 30px;
    padding: 10px 35px;
    margin-top: 20px;
    text-decoration: none;
    transition: .3s all;
  }
  .get-started:hover{
    background: var(--primary-color-light);
  }
  /* new-pane */
  .safe-pane{
    padding: 180px 40px; 
    background: var(--white-gray);
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
  }
  .safe-icons{
    font-size: 60px;
    margin-bottom: 10px;
    color: var(--primary-color);
  }
  .row-safe{
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
    gap: 20px;
    width: 100%;
  }
  .safe-block{
    max-width: 300px;
  }
  .safe-paragraph{
    font-size: 20px;
    line-height: 140%;
    color: var(--dark-gray);
  }
  .graph-pane{
    padding: 180px 40px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
  }
  .left-col{
    margin-right: 80px;
    max-width: 340px;
  }
  .start-now{
    margin-top: 30px;
  }
  .right-col{
    display: flex;
    flex-direction: column;
  }
  .youtuber-logo{
    transition: 1s all;
    height: 250px;
    max-width: 100%;
  }

  .fade-enter-from{
    opacity: 0;
    transform: translateY(100px);
  }
  .fade-leave-to{
    opacity: 0;
    transform: translateY(-100px);
  }
  .fade-enter-active{
    transition: all 1s ease-in;
  }
  #graph{
    transition: .3s all;
    max-width: 90vw;
  }
  .hide-copy{
    display: none;
  }

  @media screen and (max-width: 1066px) {
    .left-col{
      margin-right: 0;
      margin-bottom: 60px;
    }
    
  }
  @media screen and (max-width: 800px) {
    .row{
      font-size: 50px;
      font-size: 8vw;
    }
    .type-list{
      margin-left: calc(1em / 3);
      margin-top: calc(-8vw * 1.15);
      /*5 bars * 1.15 = 5.75, since ~(8*1.15 px/vw) */
      height: calc(8vw *5.75);
    }
  }
  @media screen and (max-width: 730px) {
    .row-safe{
      gap: 60px;
    }
    .safe-pane{
      padding: 70px 30px;
    }
    .graph-pane{
      padding: 60px 24px;
    }
  }
  @media screen and (max-width: 1130px) {
    .creator-img{
      width: 100%;
      min-width: 0;
      margin: 0 auto;
      transition: opacity 1s ease;
      position: absolute;
      left: max(0px, calc((100% - 460px) / 2));
    }
    .hide-img{
      opacity: 0;
    }
    .picture-pane{
      height: calc(100vw - 40px);
      max-height: 460px;
    }
    .hide-copy{
      display: block;
    }
    .text-pane{
      padding: 0 0 60px 0;
    }
  }
  @media screen and (max-width: 350px) {
    .info-box{
      padding: 0 25px;
    }
    .get-started{
      font-size: 16px;
    }
  }
  @media screen and (max-width: 450px) {
    #graph{
      margin-top: 50px;
    }
  }
  .picture-pane{
    height: calc(100vw - 40px);
    max-height: 460px;
  }

  .fade-appear-1-enter-active{
    transition: all .5s .5s ease;
  }
  .fade-appear-2-enter-active{
    transition: all .5s .65s ease;
  }
  .fade-appear-3-enter-active{
    transition: all .5s .8s ease;
  }

  .fade-appear-1-enter-from,
  .fade-appear-2-enter-from,
  .fade-appear-3-enter-from{
    opacity: 0;
    transform: translateY(50px);
  }

  .hidden-low {
    opacity: 0;
    transform: translate(0, 100px);
  }
  .showElement {
    opacity: 1;
    transform: translate(0, 0);
    -webkit-transition: all 0.5s ease-out;
    -moz-transition: all 0.5s ease-out;
    transition: all 0.5s ease-out;
  }
  .showElement2 {
    opacity: 1;
    transform: translate(0, 0);
    -webkit-transition: all 0.5s .2s ease-out;
    -moz-transition: all 0.5s .2s ease-out;
    transition: all 0.5s .2s ease-out;
  }
  .showElement3 {
    opacity: 1;
    transform: translate(0, 0);
    -webkit-transition: all 0.5s .4s ease-out;
    -moz-transition: all 0.5s .4s ease-out;
    transition: all 0.5s .4s ease-out;
  }
</style>
